import {ADD_GOAL, REMOVE_GOAL, RECEIVE_DATA} from '../actions/action_types'

export default function goals (state = [], action) {
    //console.log("goals")
          switch(action.type) {
            case ADD_GOAL :
              return state.concat([action.goal])
            case REMOVE_GOAL :
              return state.filter((goal) => goal.id !== action.id)
            case RECEIVE_DATA :
              return action.goals
            default :
              return state
          }
        }


